body {
  -ms-overflow-style: none;
  /* Internet Explorer 10+ */
  scrollbar-width: none;
  /* Firefox */
}

body::-webkit-scrollbar {
  display: none;
  /* Safari and Chrome */
}

body {
  background-color: black;
}

header {
  background-color: rgb(105, 6, 198);

}

.headerp {
  font-size: 25px;
}

.headChat {
  display: none;
}

.headChat:hover {
  color: gold;
}

.introduction {
  background-color: rgb(123, 19, 221);
}

.text1 {
  font-size: 35px;
  font-family: 'Times New Roman', Times, serif;
  font-style: italic;
}

.letstalk {
  background-color: gold;
  color: black;
  cursor: pointer;
  border: none;
  font-size: 17px;
}

.letstalk:hover {
  background-color: black;
  color: white;
}

.introimg img {
  width: 330px;
}

.pop-up {
  background-color: white;
  width: 330px;
  /* display: flex;
  justify-content: space-evenly;
  align-items: center; */
  border-radius: 10px;
  position: fixed;
  top: 50vh;
  left: 50%;
  transform: translate(-50%, -50%) scale(0.1);
  transition: transform 0.2s;
  visibility: hidden;
}

.pophead {
  width: 100px;
  position: relative;
  top: -50px;
  border-radius: 50%;
  margin-bottom: -40px;
}

.contact {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pop-ups {
  visibility: visible;
  transform: translate(-50%, -50%) scale(1);
  box-shadow: 3px 3px rgb(186, 181, 181);
}

.blurback {
  filter: blur(1.5px);
}

.popbtn {
  width: 90%;
  background-color: goldenrod;
  font-size: 20px;
  border-radius: 10px;
}

.popbtn:hover {
  background-color: black;
}

.awh {
  background-color: goldenrod;
  color: black !important;
  border: none;
  border-radius: 5px;
  padding: 10px;
  max-width: 300px;
}

.proj1,
.proj2,
.proj3 {
  /* max-width: 375px; */
  border: 1px solid goldenrod;
  border-radius: 2px;
}

.image {
  width: 100%;
  max-width: 350px;
}

.proj p {
  color: white;
  width: 75%;
  margin-top: 12px;
  font-weight: 500;
}

.proj a {
  border: none;
  outline: none;
  border-radius: 5px;
  margin-top: 5px;
  color: black;
  background-color: goldenrod;
  width: 25%;
  min-width: 85.18px;
  height: 35px;
  font-weight: bold;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.proj a:hover {
  background-color: darkgoldenrod;
}

footer {
  height: 100px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0px !important;
}

/* Media Quries */

@media screen and (min-width: 500px) {
  header {
      display: flex;
      justify-content: space-between;
      padding-left: 20px;
      padding-right: 20px;
  }


  .headChat {
      display: block;
      cursor: pointer;
      font-size: 17px;
  }

  .pop-up {
      width: 360px;
  }
}

@media screen and (min-width: 600px) {
  .introFlex {
      display: flex;
      padding-top: 50px;
      justify-content: center;
      max-width: 1000px;
  }
}
